*{
  box-sizing: border-box;
}

html{
  font-family: 'Noto Sans JP', sans-serif;
}

body{
  margin: 0;
  background-color: #3f3f3f;
}

button{
  font: inherit;
  cursor: pointer;
  background-color: #800040;
  color: white;
  border: 1px solid #800040;
  border-radius: 8px;
  padding: 0.75rem 2rem;
}

button:hover,
button:active {
  background-color: #630032;
  border-color: #630032;
}
